#NuevosLenguajesJuridicos{
    
// font-family:'Calibri'; 
opacity: 0;
animation: fadeInUp 1s ease 0.4s 1 normal forwards;
background-color: #ffffff;
color: rgb(32, 32, 32);
width: 70%;
margin: 2em auto;
padding: 3em;
box-shadow: 0 0 10px #bbbbbb;

section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 2em;

    h3{
        text-align: justify;
    }
}
h2{
    color: rgb(32, 32, 32);
    margin: .6em;
    font-size: x-large;
    font-weight: bold;
    text-decoration-line: underline;
    text-align: center;
}
h3{
    font-size: large;
    margin: .4em;
    margin-top: 1.5em;
    padding: 0 2em;
    text-align: center;
    color: rgb(32, 32, 32);
    text-decoration-line: underline;
    font-weight: bold;
}
p{
    text-align: justify;
    margin: .2em;
    color: rgb(32, 32, 32);
}
.video-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    margin: 1em auto;
}
.video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.icon{
    margin: 0 0.5em;
}
.fuente{
    text-align: left;
    font-size: small;
}

}

/*ANIMATIONS*/
@keyframes fadeInUp {
    0%      { opacity: 0; transform: translate(0,20px); }
    100%    { opacity: 1; transform: translate(0,0); }
}
